"use client"

import { useState, useEffect } from 'react'
import { Proyecto } from '@/types'

export default function ProyectosPage() {
  const [proyectos, setProyectos] = useState<Proyecto[]>([])
  const [formData, setFormData] = useState({
    idProyecto: '',
    nombreProyecto: '',
    dimensionFinanciera: '',
    administrador: '',
    aprobador: ''
  })

  useEffect(() => {
    loadProyectos()
  }, [])

  const loadProyectos = async () => {
    try {
      const response = await fetch('/api/proyectos')
      const data = await response.json()
      setProyectos(data)
    } catch (error) {
      console.error('Error loading proyectos:', error)
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    try {
      await fetch('/api/proyectos', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(formData)
      })
      setFormData({ idProyecto: '', nombreProyecto: '', dimensionFinanciera: '', administrador: '', aprobador: '' })
      loadProyectos()
    } catch (error) {
      console.error('Error creating proyecto:', error)
    }
  }

  const handleDelete = async (id: number) => {
    try {
      await fetch(`/api/proyectos?id=${id}`, { method: 'DELETE' })
      loadProyectos()
    } catch (error) {
      console.error('Error deleting proyecto:', error)
    }
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <div className="bg-white shadow-md rounded-lg p-6">
        <h1 className="text-2xl font-bold mb-6">Gestión de Proyectos</h1>
        
        <form onSubmit={handleSubmit} className="mb-6 space-y-4">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <input
              type="text"
              placeholder="ID Proyecto"
              value={formData.idProyecto}
              onChange={(e) => setFormData({...formData, idProyecto: e.target.value})}
              className="border rounded px-3 py-2"
              required
            />
            <input
              type="text"
              placeholder="Nombre del Proyecto"
              value={formData.nombreProyecto}
              onChange={(e) => setFormData({...formData, nombreProyecto: e.target.value})}
              className="border rounded px-3 py-2"
              required
            />
            <input
              type="number"
              placeholder="Dimensión Financiera"
              value={formData.dimensionFinanciera}
              onChange={(e) => setFormData({...formData, dimensionFinanciera: e.target.value})}
              className="border rounded px-3 py-2"
              required
            />
            <input
              type="text"
              placeholder="Administrador"
              value={formData.administrador}
              onChange={(e) => setFormData({...formData, administrador: e.target.value})}
              className="border rounded px-3 py-2"
              required
            />
            <input
              type="text"
              placeholder="Aprobador"
              value={formData.aprobador}
              onChange={(e) => setFormData({...formData, aprobador: e.target.value})}
              className="border rounded px-3 py-2"
              required
            />
          </div>
          <button type="submit" className="bg-green-500 text-white px-4 py-2 rounded hover:bg-green-600">
            Agregar Proyecto
          </button>
        </form>

        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">ID</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Nombre</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Dimensión</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Administrador</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Acciones</th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {proyectos.map((proyecto) => (
                <tr key={proyecto.id}>
                  <td className="px-6 py-4 whitespace-nowrap">{proyecto.idProyecto}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{proyecto.nombreProyecto}</td>
                  <td className="px-6 py-4 whitespace-nowrap">${proyecto.dimensionFinanciera.toLocaleString()}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{proyecto.administrador}</td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <button
                      onClick={() => handleDelete(proyecto.id)}
                      className="bg-red-500 text-white px-2 py-1 rounded text-sm hover:bg-red-600"
                    >
                      Eliminar
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  )
}
